import React, { useState, useEffect, Fragment } from 'react';
import { Helmet } from 'react-helmet';
import axios from 'axios';
import House from '../assets/freelance.png';

const About = () => {
    const [topSeller, setTopSeller] = useState([]);
    const [freelancer, setFreelances] = useState([]);

    useEffect(() => {
        const config = {
            headers: {
                'Content-Type': 'application/json'
            }
        };

        const getTopSeller = async () => {
            try {
                const res = await axios.get(`${process.env.REACT_APP_API_URL}/api/freelancers/topseller`, config);
                setTopSeller(res.data);
            }
            catch (err) {

            }
        };

        getTopSeller();
    }, []);

    useEffect(() => {
        const config = {
            headers: {
                'Content-Type': 'application/json'
            }
        };

        const getFreelancers = async () => {
            try {
                const res = await axios.get(`${process.env.REACT_APP_API_URL}/api/freelancers/`, config);
                setFreelances(res.data);
            }
            catch (err) {

            }
        };

        getFreelancers();
    }, []);

    const getAllFreelancers = () => {
        let allFreelancers = [];
        let results = [];

        freelancer.map(freelancer => {
            return allFreelancers.push(
                <Fragment key={freelancer.id}>
                    <div className='about__display'>
                        <img className='about__display__image' src={freelancer.photo} alt='' />
                    </div>
                    <h3 className='about__freelancer'>{freelancer.name}</h3>
                    <p className='about__contact'>{freelancer.phone}</p>
                    <p className='about__contact'>{freelancer.email}</p>
                    <p className='about__about'>{freelancer.description}</p>
                </Fragment>
            );
        });

        for(let i = 0; i < freelancer.length; i += 3) {
            results.push(
                <div key={i} className='row'>
                    <div className='col-1-of-3'>
                        {allFreelancers[i]}
                    </div>
                    <div className='col-1-of-3'>
                        {allFreelancers[i+1] ? allFreelancers[i+1] : null}
                    </div>
                    <div className='col-1-of-3'>
                        {allFreelancers[i+2] ? allFreelancers[i+2] : null}
                    </div>
                </div>
            );
        }

        return results;
    };

    const getTopSeller = () => {
        let result = [];

        topSeller.map(seller => {
            return result.push(
                <Fragment key={seller.id}>
                    <div className='about__display'>
                        <img className='about__display__image' src={seller.photo} alt='' />
                    </div>
                    <h3 className='about__topseller'>Top Seller:</h3>
                    <p className='about__freelancer'>{seller.name}</p>
                    <p className='about__contact'>{seller.phone}</p>
                    <p className='about__contact'>{seller.email}</p>
                    <p className='about__about'>{seller.description}</p>
                </Fragment>
            );
        });

        return result;
    };

    return (
        <main className='about'>
            <Helmet>
                <title>Freelance - About</title>
                <meta
                    name='description'
                    content='About us'
                />
            </Helmet>
            <header className='about__header'>
                <h1 className='about__heading'>About Realest Estate</h1>
            </header>
            <section className='about__info'>
                <div className='row'>
                    <div className='col-3-of-4'>
                        <h2 className='about__subheading'>We find the perfect freelancer for you</h2>
                        <p className='about__paragraph'>
                            Ще не вигадала що тут написати, але це місце для опису головної мети нашого сайту))))))
                        </p>
                        <div className='about__display'>
                            <img className='about__display__image' src={House} alt='' />
                        </div>
                        <p className='about__paragraph'>
                            Suspendisse gravida magna posuere purus laoreet, et elementum velit placerat. Fusce at convallis erat.
                            Curabitur placerat eros eu interdum lacinia. Nulla facilisi. Duis pretium tristique porta. Donec
                            vehicula est a massa interdum vehicula. Lorem ipsum dolor sit amet, consectetur adipiscing elit.
                            Mauris malesuada lacus mauris, eu ultrices neque egestas eu. Class aptent taciti sociosqu ad litora
                            torquent per conubia nostra, per inceptos himenaeos. Morbi elementum enim vitae purus pulvinar tincidunt.
                            Aenean id viverra leo, non vehicula odio. Vestibulum volutpat a nulla at mattis. Nam cursus semper sapien,
                            eu consequat lacus iaculis vel.
                        </p>
                    </div>
                    <div className='col-1-of-4'>
                        {getTopSeller()}
                    </div>
                </div>
            </section>
            <section className='about__team'>
                <div className='row'>
                    <h2 className='about__subheading'>Meet out awesome team!</h2>
                </div>
                {getAllFreelancers()}
            </section>
        </main>
    );
};

export default About;